<template>
  <div id="app-container">
    <app-body v-bind:contacts="contacts" v-bind:searchIndex="searchIndex" v-on:search-symbol="searchSymbol" v-on:search-field="searchWithField"></app-body>
    <app-footer v-on:item-add="itemAdd" v-bind:warningUser="warningUser" v-bind:warningPhone="warningPhone"></app-footer>
  </div>
</template>

<script>

import Body from '../src/components/Body';
import Footer from './components/Footer';
import Contacts from './model/Contacts';
import ContactItem from './model/ContactItem';


export default {
  name: 'App',
  components: {
    "app-footer": Footer,
    "app-body": Body
  },
  data: function(){
    return{
      contacts: new Contacts(),
      warningUser: -1,
      warningPhone: -1,
      searchIndex: -1
    };
  },
  methods:{
    itemAdd: function(name, number){

      if(name !== '' && number !== ''){
        let item = new ContactItem(name, number);
        this.contacts.add(item);
        this.warningUser = -1;
        this.warningPhone = -1;
      } else if(name == '' && number !== ''){
          this.warningUser = 1;
      } else if(name !== '' && number == ''){
          this.warningPhone = 1;
      } else {
        this.warningUser = 1;
        this.warningPhone = 1;
      }
    },
    searchSymbol: function(){
      this.searchIndex = 1;
    },
    searchWithField: function(searchQuery){      
      
      let result = this.contacts.items.find(function(item){
        return item.name.toLowerCase().includes(searchQuery.toLowerCase());
      });

      if(!result || searchQuery == '') {         
        alert("You do not have this contact!");
      }else {
        alert(result.name + '\n' + result.number);
      }

      this.searchIndex = -1;
    }
  }
};

</script>


<style>

body {
    background : linear-gradient(-45deg, #d8dadb, #fef1f1);
    height     : 100vh;
    margin     : 0;
    box-sizing : border-box;
    font-family: sans-serif;
}

#app-container {
    max-width    : 490px;
    margin       : 0 auto;
    height       : 95vh;
    background   : white;
    border-radius: 10px;
    box-shadow   : 0 3px 6px rgba(0, 0, 0, 0.16),
        0 3px 6px rgba(0, 0, 0, 0.23);
    transform     : translate(0, -50%);
    top           : 50%;
    position      : relative;
    display       : flex;
    flex-direction: column;
}

</style>
