<template>
    <div id="app-body" >
        <div id="header">
            <span>MY CONTACTS</span>            
            <i class="fas fa-search" v-on:click="searchSymbol" id="search-btn"></i>
        </div>
        <div :class="[searchIndex === 1 ? 'search' : 'hide']">
            <input v-on:keyup="enterKey" type="search" id="search-field" placeholder="search by name" v-model="searchQuery">
            <button id="search-field-btn" v-on:click="searchField">Search</button>
        </div>
        <all-items v-bind:contacts="contacts"></all-items>
    </div>
</template>

<script>

    import AllItems from "./AllItems";

    export default {
        name: "Body",
        components:{
            "all-items":AllItems,
        },
        props: ["contacts", "searchIndex"],
        data: function(){
            return{
                searchQuery: "",
                focused: false
            };
        },
        methods: {
            searchSymbol: function(){
                this.$emit("search-symbol");
            },
            searchField: function(){
                this.$emit("search-field", this.searchQuery);                
                this.searchQuery= '';
            },
            enterKey: function(event){
                let myButton = document.getElementById("search-field-btn");
                if(event.key === "Enter"){
                    myButton.click();
                }
            }
        }
    };

</script>


<style scoped>

#app-body {
    flex-grow: 1;
    overflow : hidden;
    display  : flex;
    flex-direction: column;
}

#header {
    display: flex;
    align-items: center;    
    justify-content: center;
    height : 70px;
    width: 100%;
    border-top-left-radius : 10px;
    border-top-right-radius: 10px;
    color: grey;
    background: #f9f9f9;
    font-size: 24px;
    flex-shrink: 0;
    position: relative;
}

#search-btn {    
    position: absolute;
    right: 7px;
    top: 17px;
    cursor: pointer;    
    padding: 6px;
    color: lightgrey;
}
/*
#search-btn::before {
    color: lightgrey;
}
*/
#search-btn:hover {
    transform: translateX(-3px);
    background-color: white;
    box-shadow: 3px 3px 3px #d6d6d6;
    color: grey;
}


::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track-piece {
    background-color: #FFF;
}

::-webkit-scrollbar-thumb {
    background-color: #c7c7c7;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #909090;
}

.search {
    display: flex;
    padding: 0 16px 12px;
    background-color: #f9f9f9;
    
}

.hide {
    display: none;
}

#search-field {
    flex-grow: 1;
    border: none;
    background-color: white;
}

#search-field:focus,
#search-field:active,
#search-field:hover {
    border : none;
    outline: none;
}


button {
    border: none;
    width: 20%;
    background-color: #eae9e9;
    cursor: pointer;
    color: grey;
    height: 26px;
    outline: none;
}

button:active {
    border: none;
}

</style>