<template>
    <footer>
        <div class="content">
            <div :class="['input-icons', warningUser === 1 ? 'warning' : '']"> 
                <i class="fa fa-user icon"></i> 
                <input v-on:keyup="focusNumberField" class="input-field" id="name-field" type="text" placeholder="my contact's name..." v-model="name"> 
            </div> 
            <div :class="['input-icons', warningPhone === 1 ? 'warning' : '']"> 
                <i class="fas fa-phone-alt icon"></i>
                <input v-on:keyup="enterData" class="input-field" id="number-field" type="text" placeholder="my contact's phone number..." v-model="number"> 
            </div>
        </div>
        <span id="add-btn" v-on:click="onClick">Add</span>
    </footer>
</template>

<script>

    export default {
        name: "Footer",
        props:["warningUser", "warningPhone"],
        data: function(){
            return{
                name: "",
                number: ""
            };
        },
        methods:{
            onClick: function(){
                this.$emit("item-add", this.name, this.number);
                if(this.name !== '' && this.number !== ''){
                    this.name = '';
                    this.number = '';
                } else {
                    this.name;
                    this.number;
                }
            },
            enterData: function(){
                let myAddButton = document.getElementById("add-btn");
                let nameField = document.getElementById("name-field");
                if(event.key === "Enter"){
                    myAddButton.click();
                    nameField.focus();
                }
            },
            focusNumberField: function(event){
                let numberField = document.getElementById("number-field");
                if(event.key === "Enter"){
                    numberField.focus();
                }
            }
        }
    };

</script>


<style scoped>

footer {
    display                   : flex;
    padding                   : 4px 4px;
    align-items               : center;
    flex-shrink               : 0;
    border-bottom-left-radius : 10px;
    border-bottom-right-radius: 10px;
    background-color: #f5f5f5;
}

.content {    
    flex-grow: 1;    

}

.input-icons { 
    display: flex;
} 

.input-field {
    border : none;
    padding: 8px;
    background: transparent;    
    box-sizing: border-box;
    flex-grow: 1;
}

.input-field:focus,
.input-field:active,
.input-field:hover {
    border : none;
    outline: none;
}

.icon { 
    padding-left: 12px; 
    width: 30px;
    height: 30px; 
    display: flex;
} 

.icon::before{
    align-self: center;
    color: grey;
}

.warning .icon::before{
    color: red;
}

#add-btn {    
    padding: 12px;
    font-weight  : bold;
    cursor       : pointer;
    color        : grey;
}

#add-btn:hover {
    transform: translateX(-3px);
    background-color: white;
    box-shadow: 3px 3px 3px #d6d6d6;;
}

</style>