<template>
    <div class="items-container">
        <span class="no-items-message">No Contacts</span>
        <item         
            v-for="item in contacts.items"
            v-bind:key="item.id"
            v-bind:item = "item"
            v-on:item-delete="onDelete(item)"
        ></item>
    </div>
</template>

<script>
    import Item from "./Item";

    export default {
        name: "AllItems",
        props:["contacts"],
        components: {
            item : Item
        },
        methods: {
            onDelete:function(item){
                this.contacts.delete(item);
            }
        }
    };

</script>


<style scoped>

.items-container {
    flex-grow: 1;
    overflow: auto;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.items-container .no-items-message {
    display: none; 
    margin: auto;
}

.no-items-message:only-child {
    display: block;
}

</style>